.cryptoCoin{
    overflow: hidden;
    text-align: center;
    border-radius: 1rem;
    position: relative;
    width: 320px;
    margin: 2rem;
    box-shadow: 5px 5px 15px 5px #FFFFFF;

}

.coinLogo{
    width: 8rem;
    margin:1.2rem;
    height: 120px;
    width: 120px;
}

.priceContainerDOWN{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border:none;
    background-color: #f00606;
    color: white;
    padding:1rem;
    
}
.priceContainerUP{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border:none;
    color: white;
    padding:1rem;
    background-color: #11da11;
}

svg{
    background-color: #11da11;
}

.priceChange{
    background-color: transparent;
    padding-left: 10px;
    text-shadow: 0 0 13px black;
}

.coinPrice{
    width: auto;
    background-color: transparent;
}

@media (max-width: 520px){
    .cryptoCoin{
        width:280px;
    }
}