@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.header{
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  margin-bottom: 0px;
}


.coinsContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  color: #fff;
}

.RandomCoinBTN{
  background-color: white;
  border: none;
  border-radius: 10px;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.RandomCoinBTN:hover{
  transition-duration: 1s;
  box-shadow: 0px 0px 38px -1px #FFFFFF;
}



@media (max-width: 520px){
  .header{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
  h1.brand{
    padding-bottom: 16px;
  }
}