@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cryptoCoin{
    overflow: hidden;
    text-align: center;
    border-radius: 1rem;
    position: relative;
    width: 320px;
    margin: 2rem;
    box-shadow: 5px 5px 15px 5px #FFFFFF;

}

.coinLogo{
    width: 8rem;
    margin:1.2rem;
    height: 120px;
    width: 120px;
}

.priceContainerDOWN{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border:none;
    background-color: #f00606;
    color: white;
    padding:1rem;
    
}
.priceContainerUP{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border:none;
    color: white;
    padding:1rem;
    background-color: #11da11;
}

svg{
    background-color: #11da11;
}

.priceChange{
    background-color: transparent;
    padding-left: 10px;
    text-shadow: 0 0 13px black;
}

.coinPrice{
    width: auto;
    background-color: transparent;
}

@media (max-width: 520px){
    .cryptoCoin{
        width:280px;
    }
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.header{
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  margin-bottom: 0px;
}


.coinsContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  color: #fff;
}

.RandomCoinBTN{
  background-color: white;
  border: none;
  border-radius: 10px;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.RandomCoinBTN:hover{
  transition-duration: 1s;
  box-shadow: 0px 0px 38px -1px #FFFFFF;
}



@media (max-width: 520px){
  .header{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
  h1.brand{
    padding-bottom: 16px;
  }
}
